.App {
  min-height: 100vh;
  font-family: sans-serif;
  /* text-align: center; */
  /* border-style: double; */
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}

/* body {
  border-style: double;
} */

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  padding: 10px;
  border: none;
}

.search-button {
  background: lightcyan;
  border: none;
  padding: 10px 20px;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.button-compare {
  padding: 10px 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}
